// src/pages/DigitalSummary.js
import React, { useState } from 'react';
import { Button, Input, Card, List, message } from 'antd';
import { VideoCameraOutlined, PlusOutlined, FileTextOutlined } from '@ant-design/icons';

const { TextArea } = Input;

const DigitalSummary = () => {
  const [videoSummary, setVideoSummary] = useState(null); // 
  const [annotations, setAnnotations] = useState([]); // 
  const [newAnnotation, setNewAnnotation] = useState(''); // 
  const [ideas, setIdeas] = useState([]); // 
  const [newIdea, setNewIdea] = useState(''); // 
  const [fullText, setFullText] = useState(''); // 
  const [isFullTextVisible, setIsFullTextVisible] = useState(false); // 

  // 
  const generateVideoSummary = () => {
    setVideoSummary('https://www.example.com/video-summary.mp4'); // 
    message.success('');
  };

  // 
  const addAnnotation = () => {
    if (newAnnotation.trim()) {
      setAnnotations([...annotations, newAnnotation]);
      setNewAnnotation('');
      message.success('');
    } else {
      message.error('');
    }
  };

  // 
  const addIdea = () => {
    if (newIdea.trim()) {
      setIdeas([...ideas, newIdea]);
      setNewIdea('');
      message.success('');
    } else {
      message.error('');
    }
  };

  // 
  const loadFullText = () => {
    setFullText(`
      1. 
      2. 
      3. 
      4. 
      5. 
    `);
    setIsFullTextVisible(true);
    message.success('');
  };

  return (
    <div style={{ padding: '20px' }}>
      <h2></h2>

      {/*  */}
      <Card title="" style={{ marginBottom: '20px' }}>
        {videoSummary ? (
          <video
            width="100%"
            controls
            src={videoSummary}
            style={{ borderRadius: '8px' }}
          />
        ) : (
          <Button
            type="primary"
            icon={<VideoCameraOutlined />}
            onClick={generateVideoSummary}
          >
            
          </Button>
        )}
      </Card>

      {/*  */}
      <Card
        title=""
        style={{ marginBottom: '20px' }}
        extra={
          <Button type="link" icon={<FileTextOutlined />} onClick={loadFullText}>
            
          </Button>
        }
      >
        {isFullTextVisible ? (
          <pre style={{ whiteSpace: 'pre-wrap' }}>{fullText}</pre>
        ) : (
          <p></p>
        )}
      </Card>

      {/*  */}
      <Card title="" style={{ marginBottom: '20px' }}>
        <List
          bordered
          dataSource={annotations}
          renderItem={(item) => <List.Item>{item}</List.Item>}
          locale={{ emptyText: '' }}
        />
        <div style={{ marginTop: '10px' }}>
          <TextArea
            rows={2}
            placeholder="..."
            value={newAnnotation}
            onChange={(e) => setNewAnnotation(e.target.value)}
            style={{ marginBottom: '10px' }}
          />
          <Button type="primary" icon={<PlusOutlined />} onClick={addAnnotation}>
            
          </Button>
        </div>
      </Card>

      {/*  */}
      <Card title="">
        <List
          bordered
          dataSource={ideas}
          renderItem={(item) => <List.Item>{item}</List.Item>}
          locale={{ emptyText: '' }}
        />
        <div style={{ marginTop: '10px' }}>
          <TextArea
            rows={2}
            placeholder="..."
            value={newIdea}
            onChange={(e) => setNewIdea(e.target.value)}
            style={{ marginBottom: '10px' }}
          />
          <Button type="primary" icon={<PlusOutlined />} onClick={addIdea}>
            
          </Button>
        </div>
      </Card>
    </div>
  );
};

export default DigitalSummary;
