// src/pages/FrameworkUploadEdit.js
import React, { useState, useEffect } from 'react';
import { Upload, Button, Input, message, Card } from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import { useLocation, useNavigate } from 'react-router-dom';

const defaultFramework = `
1. 
2. 
3. 
4. 
5. 
`;

const FrameworkUploadEdit = () => {
  const location = useLocation(); // 
  const navigate = useNavigate(); // 
  const [framework, setFramework] = useState(''); // 
  const [uploaded, setUploaded] = useState(false); // 
  const [currentTopic, setCurrentTopic] = useState(''); // 

  // 
  useEffect(() => {
    if (location.state?.topic) {
      setCurrentTopic(location.state.topic);
    }
  }, [location.state]);

  // 
  const handleUpload = (info) => {
    const reader = new FileReader();
    reader.onload = (e) => {
      const content = e.target.result;
      setFramework(content);
      setUploaded(true);
      message.success('');
    };
    reader.readAsText(info.file);
  };

  // 
  const generateFramework = () => {
    setFramework(defaultFramework);
    setUploaded(true);
    message.success('');
  };

  // 
  const handleFrameworkChange = (e) => {
    setFramework(e.target.value);
  };

  return (
    <div>
      <h2></h2>

      {/*  */}
      <Card title="" style={{ marginBottom: '20px' }}>
        {currentTopic ? (
          <p>{currentTopic}</p>
        ) : (
          <p></p>
        )}
        <Button type="link" onClick={() => navigate('/topic-input')}>
          
        </Button>
      </Card>

      {/*  */}
      <Upload
        beforeUpload={(file) => {
          handleUpload({ file });
          return false; // 
        }}
        showUploadList={false}
      >
        <Button icon={<UploadOutlined />}>txt  word</Button>
      </Upload>

      <Button 
        type="default" 
        onClick={generateFramework} 
        style={{ marginLeft: '10px' }}
      >
        
      </Button>

      {/*  */}
      {uploaded && (
        <Card title="" style={{ marginTop: '20px' }}>
          <Input.TextArea
            rows={10}
            value={framework}
            onChange={handleFrameworkChange}
          />
          <Button 
            type="primary" 
            style={{ marginTop: '10px' }}
            onClick={() => message.success('')}
          >
            
          </Button>
        </Card>
      )}
    </div>
  );
};

export default FrameworkUploadEdit;


