// src/pages/PaperGeneration.js
import React, { useState } from 'react';
import { Button, Input, Select, Card, message } from 'antd';

const { TextArea } = Input;
const { Option } = Select;

const subTitles = [
  '',
  '',
  '',
  '',
  ''
]; // 

const PaperGeneration = () => {
  const [selectedSubtitle, setSelectedSubtitle] = useState(''); // 
  const [prompt, setPrompt] = useState(''); //  Prompt
  const [generatedText, setGeneratedText] = useState(''); // 

  // 
  const generateText = () => {
    let text;
    if (prompt) {
      text = ` "${prompt}"  ${selectedSubtitle} \n\n...`;
    } else {
      text = ` ${selectedSubtitle} \n\n...`;
    }
    setGeneratedText(text);
    message.success(` ${selectedSubtitle} `);
  };

  return (
    <div style={{ padding: '20px' }}>
      <h2></h2>

      {/*  */}
      <Select
        value={selectedSubtitle}
        onChange={setSelectedSubtitle}
        style={{ width: '300px', marginBottom: '20px' }}
      >
        {subTitles.map((title) => (
          <Option key={title} value={title}>
            {title}
          </Option>
        ))}
      </Select>

      {/* Prompt  */}
      <TextArea
        placeholder=""
        value={prompt}
        onChange={(e) => setPrompt(e.target.value)}
        rows={3}
        style={{ marginBottom: '10px' }}
      />

      {/*  */}
      <Button type="primary" onClick={generateText} style={{ marginBottom: '20px' }}>
        
      </Button>

      {/*  */}
      <Card title={`${selectedSubtitle}`} bordered={false}>
        <pre>{generatedText}</pre>
      </Card>
    </div>
  );
};

export default PaperGeneration;
