// src/pages/FinalPolish.js
import React, { useState, useRef } from 'react';
import { Input, Button, Upload, Card, List, Modal, message, Space } from 'antd';
import { UploadOutlined, PictureOutlined } from '@ant-design/icons';

const { TextArea } = Input;

const databaseImages = ['1', '2', '3']; // 

const FinalPolish = () => {
  const [finalText, setFinalText] = useState(''); // 
  const [summary, setSummary] = useState(''); // 
  const [keywords, setKeywords] = useState(''); // 
  const [selectedImage, setSelectedImage] = useState(null); // 
  const [isModalVisible, setIsModalVisible] = useState(false); // 
  const [beautifyModalVisible, setBeautifyModalVisible] = useState(false); // 
  const [beautifiedText, setBeautifiedText] = useState(''); // 
  const textAreaRef = useRef(null); //  TextArea 

  // 
  const handleSelectText = () => {
    const textarea = textAreaRef.current.resizableTextArea.textArea;
    const selected = textarea.value.substring(
      textarea.selectionStart,
      textarea.selectionEnd
    );

    if (selected) {
      setBeautifiedText(selected.replace(/\./g, '')); // 
      setBeautifyModalVisible(true); // 
    } else {
      message.error('');
    }
  };

  // 
  const handleReplaceText = () => {
    const textarea = textAreaRef.current.resizableTextArea.textArea;
    const { value, selectionStart, selectionEnd } = textarea;

    const newText =
      value.substring(0, selectionStart) +
      beautifiedText +
      value.substring(selectionEnd);

    setFinalText(newText); // 
    setBeautifyModalVisible(false); // 
    message.success('');
  };

  // 
  const generateSummary = () => {
    setSummary('...');
    message.success('');
  };

  // 
  const generateKeywords = () => {
    setKeywords('1, 2, 3');
    message.success('');
  };

  // 
  const showImageModal = () => {
    setIsModalVisible(true);
  };

  // 
  const handleSelectImage = (image) => {
    setSelectedImage(image);
    setIsModalVisible(false);
    message.success(` ${image}`);
  };

  // 
  const handleImageUpload = (info) => {
    message.success(`${info.file.name} `);
    setSelectedImage(info.file.name); // 
  };

  // 
  const insertCitations = () => {
    const textWithCitations = `${finalText}\n\n[1] `;
    setFinalText(textWithCitations);
    message.success('');
  };

  // 
  const handleSaveFinal = () => {
    message.success('');
  };

  return (
    <div style={{ padding: '20px' }}>
      <h2></h2>

      {/*  */}
      <TextArea
        ref={textAreaRef}
        rows={10}
        placeholder="..."
        value={finalText}
        onChange={(e) => setFinalText(e.target.value)}
        style={{ marginBottom: '20px' }}
      />

      {/*  */}
      <Space wrap size="middle" style={{ marginBottom: '20px' }}>
        <Button type="primary" onClick={generateSummary}>
          
        </Button>
        <Button type="primary" onClick={generateKeywords}>
          
        </Button>
        <Button type="primary" onClick={showImageModal}>
          
        </Button>
        <Upload beforeUpload={() => false} onChange={handleImageUpload} showUploadList={false}>
          <Button icon={<UploadOutlined />}></Button>
        </Upload>
        <Button type="primary" onClick={insertCitations}>
          
        </Button>
        <Button type="primary" onClick={handleSelectText}>
          
        </Button>
        <Button type="primary" onClick={handleSaveFinal}>
          
        </Button>
      </Space>

      {/*  */}
      {summary && (
        <Card title="" style={{ marginTop: '20px' }}>
          <p>{summary}</p>
        </Card>
      )}

      {/*  */}
      {keywords && (
        <Card title="" style={{ marginTop: '20px' }}>
          <p>{keywords}</p>
        </Card>
      )}

      {/*  */}
      {selectedImage && (
        <Card title="" style={{ marginTop: '20px' }}>
          <PictureOutlined style={{ fontSize: '24px', marginRight: '10px' }} />
          <span>{selectedImage}</span>
        </Card>
      )}

      {/*  */}
      <Modal
        title=""
        visible={isModalVisible}
        onCancel={() => setIsModalVisible(false)}
        footer={null}
      >
        <List
          bordered
          dataSource={databaseImages}
          renderItem={(item) => (
            <List.Item onClick={() => handleSelectImage(item)}>
              {item}
            </List.Item>
          )}
        />
      </Modal>

      {/*  */}
      <Modal
        title=""
        visible={beautifyModalVisible}
        onOk={handleReplaceText}
        onCancel={() => setBeautifyModalVisible(false)}
        okText=""
        cancelText=""
      >
        <TextArea
          rows={5}
          value={beautifiedText}
          onChange={(e) => setBeautifiedText(e.target.value)}
        />
      </Modal>
    </div>
  );
};

export default FinalPolish;
